.main-sm {
  div {
    @include flexbox();
    @include text-style-20();
    width: 100vw;
    height: 30vh;
    padding-left: 8px;
    margin-bottom: 2px;
    color: white;
    text-shadow: 0px 0px 3px rgb(75, 75, 69);
  }
  .sm-about {
    display: block;
    background: url(./images/main-cut.jpg) no-repeat center/cover;
  }
  .sm-menu {
    display: block;
    background: url(./images/menu-cut.jpeg) no-repeat center/cover;
  }
  .sm-shop {
    display: block;
    background: url(./images/shop.jpeg) no-repeat center/cover;
  }
}

@include responsive(lg) {
  .main-lg {
    @include flexbox();
    width: 100vw;
    height: 95vh;
    background: url(./images/main.jpg) no-repeat center/cover;
  }
}
